var _self;

export default{
	
	data() {
		return {
			currentIndex:0,
			payMethod:"wxpay",
			pay_type:"wxPay",
			orderInfo:"",
			orderId:"",
			payNum:0,
			loginInfo:{},
			isPayOk:false
		}
	},
	onLoad(opt){
		_self = this;
		console.log(opt);
		var loginRes = this.checkLogin('../index/recommend', '2');
		if(!loginRes){return false;}
		this.loginInfo = loginRes;
		if(opt.orderid != undefined && opt.paynum != undefined){
			_self.orderId = opt.orderid;
			_self.payNum = opt.paynum * 1;
		}
	},
	components:{
		
	},
	methods:{
		//微信小程序支付
		wxappPay(ress){
			var payObj = ress.data.result.result;
			
			console.log(payObj);
			console.log("签名：",payObj.paySign);
			uni.requestPayment({
				timeStamp:payObj.timeStamp,
				nonceStr:payObj.nonceStr,
				package:payObj.package,
				signType:"MD5",
				paySign:payObj.paySign,
			    success: function (res) {
			        //console.log('success:' + JSON.stringify(res));
					uni.showToast({
						title:"恭喜升级掌柜",
						icon:"success"
					})
					
					_self.isPayOk = true;
					
					setTimeout(function(){
						uni.switchTab({
							url:"../index/recommend"
						})
					},3000);
					
			    },
			    fail: function (err) {
			        console.log('fail:' + JSON.stringify(err));
			    }
			});
		},
		//确认支付
		confirmPay(){
			var payObjMax = {
				user_id:_self.loginInfo.uid,
				order_sn:_self.orderId,
				pay_code:_self.pay_type,
				type:"1",
				openid:_self.loginInfo.openid
			}
			

				payObjMax.type = "2";

			console.log("sdfsdf:",payObjMax);
			uni.request({
			    url: _self.$httpUrl+'index.php/api/recommend/orderPaying', 
				method:"POST",
				data:payObjMax,
			    success: (ress) => {
			     
			      console.log(ress);
				  
				  if(ress.data.status != -1){
					
					if(_self.payMethod == "wxpay"){
						

							
							_self.wxappPay(ress);
							

						






































					}else if(_self.payMethod == "alipay"){
						uni.requestPayment({
						    provider: _self.payMethod,
						    orderInfo: ress.data.data, //微信、支付宝订单数据
						    success: function (res) {
						        //console.log('success:' + JSON.stringify(res));
								uni.showToast({
									title:"恭喜升级掌柜",
									icon:"success"
								})
								
								_self.isPayOk = true;
								
								setTimeout(function(){
									uni.switchTab({
										url:"../index/recommend"
									})
								},3000);
								
						    },
						    fail: function (err) {
						        //console.log('fail:' + JSON.stringify(err));
						    }
						});					  
					}
					
					
				  }else{
					  uni.showToast({
					  	title:ress.data.msg,
						icon:""
					  })
				  }
				  
			    },fail: (e) => {
					
			    },complete: (e) => {
					
			    }
			});
			
			
			
			
			
		},
		changePayType(i){
			_self.currentIndex = i;
			if(i == 0){
				_self.payMethod = "wxpay";
				_self.pay_type = "wxPay";
			}else{
				_self.payMethod = "alipay";
				_self.pay_type = "aliPay";
			}
		},
		backBeforePage: ()=>{
			
			uni.navigateBack({
				delta:1
			})
		}
		
		
	}
	
	
	
}


